import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./assets/icon/iconfont.css";
import "./assets/base/reset.css";
//vue-socket 
import VueSocketIO from "vue-socket.io";

Vue.use(
  new VueSocketIO({
    debug: true,
    // 
    connection: "http://localhost:8080",
    vuex: {}
  })
);
//300
import fast from "fastclick";
fast.attach(document.body);

//
import { Notify } from "vant";
Vue.use(Notify);

//button
import { Button } from "vant";
Vue.use(Button);
//
import { CountDown } from "vant";
Vue.use(CountDown);
//
import { NavBar } from "vant";
Vue.use(NavBar);

//axios
import axios from "axios";
import vaxios from "vue-axios";
Vue.use(vaxios, axios);

//vue
Vue.config.productionTip = false;

//
import { Dialog } from "vant";
Vue.use(Dialog);

//
window.time = 60 * 15;
window.start = 0;

//axios 
axios.interceptors.request.use(
  config => {
    if (sessionStorage.getItem("islogoin") == "true") {
      if (sessionStorage.getItem("token")) {
        config.headers.token = sessionStorage.getItem("token");
      }
    } else {
      sessionStorage.clear();
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

//axios 
axios.interceptors.response.use(
  response => {
    if (response.data.resultCode == "401") {
      console.log("response.data.resultCode404");
      sessionStorage.clear();
      window.location.href = "/logoin";
      return;
    } else {
      return response;
    }
  },
  error => {
    return Promise.reject(error.response); // 
  }
);

//
router.beforeEach((to, from, next) => {
  if (to.path === "/logoin" || to.path === "/" || to.path === "/registry") {
    next();
  } else {
    if (sessionStorage.getItem("islogoin") == "true") {
      next();
    } else {
      next("/logoin");
    }
  }
});

//VUE
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
